* {
  box-sizing: border-box;
}

html {
  font-family: Arial, sans-serif;
  font-size: 20px;
  line-height: 23px;
  color: rgba(47, 79, 79, 1);
}

body {
  margin: 0;
}

.people {
  position: relative;

  padding: 20px 25px;

  --columns: 2;

  display: grid;
  grid-template-columns: repeat(var(--columns), 220px);
  justify-content: center;
  gap: 30px 10px;

  @media (min-width: 1000px) {
    --columns: 4;
  }
}

.person {
  position: relative;

  text-align: center;

  background-color: transparent;

  &:hover {
    z-index: 1;

    .photo {
      transform: scale(1.2);
      margin-bottom: 25px;

      & img {
        border-radius: 60px;
      }
    }

    &::after {
      content: '';

      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      z-index: -1;

      height: 100%;

      transform: scale(1.2);

      background-color: rgba(238, 238, 238, 1);
      border-radius: 10px;
    }

    .extra {
      display: block;
    }
  }
}

.photo {
  position: relative;

  display: block;

  margin: 0 auto;

  max-width: 200px;

  transition: transform 1s;

  & img {
    width: 100%;
    border-radius: 50%;
  }
}

.rank {
  display: flex;
  justify-content: center;
  align-items: center;

  font-size: 16px;
  color: rgba(139, 0, 0, 1);
  font-weight: 700;

  width: 32px;
  height: 32px;
  border-radius: 30px;
  background-color: #fff;

  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}


.name {
  line-height: 23px;
  color: rgba(47, 79, 79, 1);
  font-weight: 700;
}

.extra {
  display: none;
  margin-top: 9px;
/*   margin-bottom: 16px; */

  font-size: 18px;
  line-height: 20px;
  color: rgba(0, 0, 0, 1);
}

.sport {

  &:after {
    content: ',';
  }
}

.pay {
  &:before {
    content: '$';
  }

  &:after {
    content: 'M';
  }
}